
import { PORT1 } from "@/api/config/servicePort"
import qs from "qs"

import http from "@/api"

import { Login } from "@/api/interface"

/**
 * @name 
 */
// * 
export const loginApi = (params: Login.ReqLoginForm): any => {
	// return http.post(PORT1 + `/login`)
	// return http.post(PORT1 + `/login`, {}, { params }) // post  query   ==>  ?username=admin&password=123456
	return http.post(PORT1 + `/login`, qs.stringify(params)) // post     ==>  application/x-www-form-urlencoded
	// return http.post(PORT1 + `/login`, params, { headers: { noLoading: true } }) //  loading
}

// * 
export const getAuthorButtons = () => {
	return http.get(PORT1 + `/auth/buttons`)
}

// * 
export const getMenuList = () => {
	return http.get(PORT1 + `/menu/list`)
}